import React, { Fragment, useState, useEffect, useRef } from "react";
import {
  Cell,
  Counter,
  Div,
  MiniInfoCell,
  Button,
  Input,
  FormLayout,
  Snackbar,
} from "@vkontakte/vkui";
//import bridge from "@vkontakte/vk-bridge";

const Delivery = ({
  deliv,
  sum,
  activePVZ,
  go,
  typeDelivery,
  order,
  who,
  activeCity,
  clearCart,
  setOrders_success,
                    fetchedUser
}) => {
  const payRef = useRef(null);
  const [street, setStreet] = useState("");
  const [house, setHouse] = useState("");
  const [room, setRoom] = useState("");
  const [phone, setPhone] = useState("");
  const [snackbar, setSnackbar] = useState(null);
  const [payLink, setPayLink] = useState(null);
  const [jsonParams, setJsonParams] = useState(null);
  const [orderNum, setOrderNum] = useState(null);

  useEffect(() => {
    setPayLink(null);
    const date = Date.now();
    setOrderNum(date);
    const jsonParams = {};
    for (let key in order) {
      if (key !== "meta")
        jsonParams[
          key
        ] = `${order[key].item.title} (${order[key].item._id})  Штук - ${order[key].count}`;
    }
    jsonParams.amount = order.meta.sum;
    jsonParams.count = order.meta.count;
    jsonParams.weight = order.meta.weight;
    jsonParams.typeDelivery = who;
    jsonParams.activePVZ = activePVZ !== null ? activePVZ.name : "Не выбрано";
    jsonParams.address = `г. ${activeCity.name},ул. ${street},д. ${house},кв. ${room}`;
    jsonParams.phone = phone;
    jsonParams.orderNum = orderNum;
    setJsonParams(jsonParams);
    fetch("https://sahpossum.herokuapp.com/pay/sber", {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      mode: "cors", // no-cors, cors, *same-origin
      cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
      credentials: "same-origin", // include, *same-origin, omit
      headers: { "Content-Type": "application/json" },
      redirect: "follow", // manual, *follow, error
      referrer: "no-referrer", // no-referrer, *client
      body: JSON.stringify({
        amount: Math.round(Number(deliv.price)) + Number(sum),
        orderNumber: date,
        jsonParams: jsonParams,
      }),
    })
      .then((res) => res.json())
      .then((res) => {
        console.log(res);
        setPayLink(String(res)); //+'toWeb2App=true'
      })
      .catch((e) => console.log(e));
  }, [order, deliv, street, house, room, phone]);
  const pay = (e) => {
    e.preventDefault();
    try {
      if (who === "sdek") {
        if (!activePVZ) throw "Не выбран пункт доставки";
        if (phone === "") throw "Не указан номер телефона";
        if (activePVZ.name === "Доставить домой") {
          if (street === "") throw "Не указан адрес доставки";
          if (house === "") throw "Не указан адрес доставки";
          if (room === "") throw "Не указан адрес доставки";
          if (phone === "") throw "Не указан номер телефона";
        }
      }
      if (who === "PR") {
        if (street === "") throw "Не указан адрес доставки";
        if (house === "") throw "Не указан адрес доставки";
        if (room === "") throw "Не указан адрес доставки";
        if (phone === "") throw "Не указан номер телефона";
      }
      document.getElementById("payLink").style.display = "block";
      // setSnackbar(
      //   <Snackbar layout="vertical" onClose={() => setSnackbar(null)}>
      //     <pre>
      //       Ссылка на оплату под кнопкой.
      //       В сбер отправится:
      //       {JSON.stringify(jsonParams, true, 2)}
      //     </pre>
      //   </Snackbar>
      // );
      // if (fetchedUser.id){
      //     bridge.send("VKWebAppOpenPayForm", {"app_id": 7600142, "action": "pay-to-user", "params": {
      //             "amount": order.meta.sum +Math.round(Number(deliv.price)),
      //             "user_id": 199500866,
      //             "description": `Оплата товаров на сумму ${order.meta.sum +Math.round(Number(deliv.price))} в магазине "Сахарный поссум"`
      //         },
      //     });
      // }
    } catch (e) {
      setSnackbar(
        <Snackbar layout="vertical" onClose={() => setSnackbar(null)}>
          {e}
        </Snackbar>
      );
    }
  };
  const payAway = () => {
    const orders_success = {sum: Math.round(Number(deliv.price)) + Number(sum), num: orderNum, user: fetchedUser !== null ? fetchedUser.id : 0, jsonParams: jsonParams};
    clearCart()
    fetch("https://sahpossum.herokuapp.com/pay/payaway", {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      mode: "cors", // no-cors, cors, *same-origin
      cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
      credentials: "same-origin", // include, *same-origin, omit
      headers: { "Content-Type": "application/json" },
      redirect: "follow", // manual, *follow, error
      referrer: "no-referrer", // no-referrer, *client
      body: JSON.stringify(orders_success),
    })
        .then(res=>res.json())
        .then(res=>{
          localStorage.setItem("orders_success", JSON.stringify(res));
          setOrders_success(res);
        })
  }

  if (deliv.error) {
    return (
      <>
        <MiniInfoCell textWrap={"full"}>{deliv.error}</MiniInfoCell>
      </>
    );
  }

  return (
    <Fragment>
      <MiniInfoCell
        after={<Counter>{Math.round(Number(deliv.price))} руб.</Counter>}
      >
        Стоимость доставки
      </MiniInfoCell>
      <MiniInfoCell after={deliv.deliveryDateMax}>Доставка до:</MiniInfoCell>
      <MiniInfoCell after={Math.round(Number(deliv.price)) + Number(sum)}>
        Итого к оплате:{" "}
      </MiniInfoCell>
      {typeDelivery === "sdek" && (
        <Cell
          expandable
          indicator={activePVZ !== null ? activePVZ.name : "Не выбрано"}
          onClick={() => go("GetPvzList")}
        >
          Пункт выдачи
        </Cell>
      )}
      {who === "sdek" && (
        <Cell
          expandable
          onClick={() => go("GetPvzList")}
          indicator={activePVZ !== null ? activePVZ.name : "Не выбрано"}
        >
          Место доставки
        </Cell>
      )}
      {who === "sdek" && activePVZ !== null && (
        <FormLayout>
          <Input
            value={phone}
            onChange={(e) => setPhone(e.target.value)}
            top={"Телефон"}
          />
          {activePVZ.name === "Доставить домой" && (
            <FormLayout>
              <Input
                value={street}
                onChange={(e) => setStreet(e.target.value)}
                top={"Улица"}
              />
              <Input
                value={house}
                onChange={(e) => setHouse(e.target.value)}
                top={"Дом"}
              />
              <Input
                value={room}
                onChange={(e) => setRoom(e.target.value)}
                top={"Квартира"}
              />
            </FormLayout>
          )}
        </FormLayout>
      )}
      {who === "PR" && (
        <FormLayout>
          <Input
            value={street}
            onChange={(e) => setStreet(e.target.value)}
            top={"Улица"}
          />
          <Input
            value={house}
            onChange={(e) => setHouse(e.target.value)}
            top={"Дом"}
          />
          <Input
            value={room}
            onChange={(e) => setRoom(e.target.value)}
            top={"Квартира"}
          />
          <Input
            value={phone}
            onChange={(e) => setPhone(e.target.value)}
            top={"Телефон"}
          />
        </FormLayout>
      )}
      <Div>
        {payLink && (
          <>
            <a
              style={{ display: "none" }}
              target="_blank"
              ref={payRef}
              id="payLink"
              href={payLink}
              onClick={payAway}
            >
              Нажмите сюда для перехода к оплате заказа
            </a>
            <Button onClick={pay} mode={"outline"} size={"xl"}>
              {`Оплатить ${Math.round(Number(deliv.price)) + Number(sum)} руб.`}
            </Button>
          </>
        )}
      </Div>
      {snackbar}
    </Fragment>
  );
};
export default Delivery;
